import mSearch from '../../components/m-search/m-search.vue'
export default {
	components: {
		mSearch
	},
	name: 'slide-list',
	computed: {
		Screen_width() {
			return uni.getSystemInfoSync().windowWidth;
		}
	},
	data() {
		return {
			img: '../../static/slide-list/qr_code.png',
			visible: false,
			start_slide_x: 0,
			btnWidth: 0,
			startX: 0,
			LastX: 0,
			startTime: 0,
			screenName: '',
			list : [
			],
			btuBottom: '0',
			secondHeight: ''
		};
	},
	onShow() {
		const res = uni.getSystemInfoSync();
		this.secondHeight = res.windowHeight;
		this.findAccount ()
	},
	created () {
		this.findAccount()
	},
	methods: {
		//查询所有
		findAccount () {
			uni.request({
			    url: '/v1/findAccount',
			    success: (res) => {
					this.list = res.data
			    }
			})
		},
		cancelEvent(){
			this.visible = false
		},
		search(e, val) {
			this.screenName = e
			console.log('点击搜索')
		},
		addCustomer(){
			uni.navigateTo({
			    url: '/pages/account/addAccount'
			});
		},
		getDetail(item){
			uni.navigateTo({
			    url: `/pages/account/upAccount?id=${item.id}`
			})
		}
	}
}
